@charset "UTF-8";
/* Normalize -------------------------------------------------------------------- */
*,
:after,
:before {
  box-sizing: border-box;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote,
pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset,
form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas,
details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section,
summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  max-width: 100%;
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  outline: none;
  border-radius: 0;
  -webkit-appearance: none;
}

button,
input {
  overflow: visible;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

ol, ul {
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

textarea {
  resize: vertical;
  overflow: auto;
}

::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  overflow: visible;
}

button,
select {
  text-transform: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

select::-ms-expand {
  display: none;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

.placeholder {
  line-height: normal;
}

:-moz-placeholder {
  line-height: normal;
}

::-moz-placeholder {
  line-height: normal;
}

:-ms-input-placeholder {
  line-height: normal;
}

::-webkit-input-placeholder {
  line-height: normal;
}

/* Fonts -------------------------------------------------------------------- */
@font-face {
  font-family: 'TTNorms';
  src: url("../fonts/TTNorms-Regular.ttf") format("ttf"), url("../fonts/TTNorms-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'TTNorms';
  src: url("../fonts/TTNorms-Medium.ttf") format("ttf"), url("../fonts/TTNorms-Regular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'TTNorms';
  src: url("../fonts/TTNorms-Bold.ttf") format("ttf"), url("../fonts/TTNorms-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy/Gilroy-Medium.ttf") format("ttf"), url("../fonts/Gilroy/Gilroy-Medium.woff") format("woff"), url("../fonts/Gilroy/Gilroy-Medium.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

/* Classes -------------------------------------------------------------------- */
.container {
  width: 100%;
  padding: 0 15px;
  max-width: 1375px;
  margin: 0 auto;
}

.title-main {
  color: #000;
  font-size: 50px;
  font-weight: 700;
  line-height: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.title-main span {
  color: #0041ff;
}

.title-content {
  color: #000;
  font-size: 45px;
  font-weight: 700;
  line-height: 55px;
}

.button {
  width: 100%;
  max-width: 233px;
}

.button button {
  width: 100%;
  padding: 28px 15px 24px;
  color: #000;
  font-weight: 700;
  border-radius: 8px;
  background-color: #ffde03;
  font-size: 16px;
  letter-spacing: 0.16px;
  line-height: 18px;
  transition: all 0.2s;
}

.button button:hover {
  background-color: #fcca00;
}

.input-block {
  position: relative;
  max-width: 360px;
  width: 100%;
  min-height: 103px;
}

.input-block.active input.error ~ .label {
  color: #ef3124;
}

.input-block.active textarea.error ~ .label {
  color: #ef3124;
}

.input-block .label {
  max-height: 70px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.37);
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.16px;
  padding: 23px 32px 23px;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.25s cubic-bezier(0.2, 0, 0.03, 1);
  pointer-events: none;
}

.input-block.active .label, .input-block input:focus ~ .label,
.input-block textarea:focus ~ .label {
  font-size: 13px;
  font-weight: 400;
  line-height: 13px;
  letter-spacing: 0.52px;
  background-color: transparent;
  padding: 4px 8px;
  color: #0041ff;
  z-index: 2;
  top: -11px;
  left: 25px;
  transition: all 0.125s cubic-bezier(0.2, 0, 0.03, 1);
}

.input-block.active .label::before, .input-block input:focus ~ .label::before,
.input-block textarea:focus ~ .label::before {
  content: "";
  position: absolute;
  z-index: -1;
  max-width: 100%;
  width: 100%;
  height: 10px;
  background-color: #fff;
  left: 0;
  bottom: 1px;
  transition: all 0.125s cubic-bezier(0.2, 0, 0.03, 1);
}

.input-block input,
.input-block textarea {
  max-height: 70px;
  padding: 27px 32px 23px;
  border-radius: 8px;
  border: 1px solid #d7d7d7;
  background-color: #fff;
  width: 100%;
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  letter-spacing: 0.16px;
  color: #000;
  transition: all 0.125s cubic-bezier(0.2, 0, 0.03, 1);
}

.input-block input:focus,
.input-block textarea:focus {
  border: 1px solid #3662ff;
  outline: 0;
}

.input-block textarea {
  width: 100%;
  padding: 28px 32px 24px;
  line-height: 18px;
  max-height: 118px;
  height: 100vh;
  resize: none;
}

.input-block input.error,
.input-block textarea.error {
  border: 1px solid #ef3124;
  transition: all 0.2s;
}

.input-block input.error:focus,
.input-block textarea.error:focus {
  border: 1px solid #ef3124;
  transition: all 0.2s;
}

.input-block input.error:focus ~ .label,
.input-block textarea.error:focus ~ .label {
  opacity: 1;
  color: #ef3124;
  transition: all 0.2s;
}

.input-block input.valid:focus ~ .label,
.input-block textarea.valid:focus ~ .label {
  opacity: 1;
  color: #3662ff;
  transition: all 0.2s;
}

.textarea-block {
  min-height: 151px;
}

.checkbox-block {
  max-width: 100%;
  width: 100%;
  height: 25px;
  display: flex;
  align-items: center;
  outline: none;
  cursor: pointer;
}

.checkbox-block .custom-checkbox {
  max-width: 25px;
  width: 100%;
  height: 25px;
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.checkbox-block .custom-checkbox:checked ~ .label::before {
  border-color: #ffde03;
  background-color: #ffde03;
  background-image: url("../img/svg/Done.svg");
  transition: all 0.2s;
}

.checkbox-block .label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
}

.checkbox-block .label::before {
  content: "";
  display: inline-block;
  width: 25px;
  height: 25px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #d7d7d7;
  background-color: #fff;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  transition: all 0.2s;
}

.step__checkbox-wrapper {
  display: flex;
  align-items: center;
}

.step__checkbox-wrapper .checkbox-text {
  padding-top: 0;
  display: inline;
  max-width: 100%;
  text-align: left;
  margin-left: 15px;
  letter-spacing: 0.18px;
}

.step__checkbox-wrapper .checkbox-text span {
  font-weight: 700;
  color: #414141;
}

.step__navigation {
  display: block;
  margin-right: auto;
  position: relative;
  max-width: 78px;
  width: 100%;
}

.step__navigation a {
  padding-left: 36px;
  font-size: 14px;
  font-weight: 700;
  line-height: 22px;
  letter-spacing: 0.56px;
  color: #aeaeae;
}

.step__navigation a::before {
  position: absolute;
  content: "";
  background: url(../img/svg/Arrow.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 18px;
  height: 13px;
  left: 0;
  top: 10px;
}

.step__wrapper span:nth-child(2) {
  font-size: 14px;
  font-weight: 700;
  line-height: 22px;
  text-transform: uppercase;
  letter-spacing: 1.68px;
  color: #0041ff;
  display: block;
  margin: 0 auto;
  max-width: 340px;
  width: 100%;
  text-align: center;
  padding-top: 40px;
}

.step__wrapper h1 {
  text-align: center;
  max-width: 620px;
  width: 100%;
  margin: 0 auto;
  line-height: 50px;
  font-size: 45px;
  display: block;
  padding-top: 10px;
}

.step__wrapper p {
  text-align: center;
  letter-spacing: 0.18px;
  padding-top: 40px;
  max-width: 620px;
  width: 100%;
  margin: 0 auto;
}

.step__price {
  display: flex;
  align-items: baseline;
  max-width: 150px;
  width: 100%;
  padding-top: 12px;
}

.step__price span {
  padding-top: 0;
  letter-spacing: 0.18px;
}

.step__price span:nth-child(2) {
  padding-top: 0;
  text-align: left;
  margin: 0;
  max-width: 97px;
  font-size: 33px;
  font-weight: 500;
  line-height: 35px;
  letter-spacing: 0.33px;
  padding-left: 10px;
}

.step__price span:nth-child(3) {
  padding-top: 0;
  max-width: 100px;
  font-size: 33px;
  font-weight: 500;
  line-height: 35px;
  letter-spacing: 0.33px;
  color: #0041ff;
}

.remove-driver-btn {
  position: relative;
  cursor: pointer;
}

.remove-driver-btn button {
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  line-height: 33px;
  letter-spacing: 0.16px;
  color: rgba(0, 0, 0, 0.4);
  background-color: transparent;
  border: none;
  padding: 0;
  padding-left: 28px;
  transition: all 0.2s;
}

.remove-driver-btn::before {
  content: "";
  position: absolute;
  background: url(../img/svg/Delete.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 19px;
  height: 19px;
  left: -4px;
  top: 7px;
  transition: all 0.3s;
}

.remove-driver-btn::after {
  content: "";
  position: absolute;
  background: url(../img/delete-red.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  width: 13px;
  height: 15px;
  bottom: 9px;
  left: -1px;
  opacity: 0;
  transition: all 0.2s;
}

.remove-driver-btn:hover button {
  color: #ef3124;
}

.remove-driver-btn:hover::after {
  opacity: 1;
}

.addEdit-button {
  position: relative;
  cursor: pointer;
  max-width: 185px;
  width: 100%;
  margin-right: auto;
}

.addEdit-button button {
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0.16px;
  color: #0041ff;
  background-color: transparent;
  border: none;
  padding: 0;
  padding-left: 30px;
  transition: all 0.2s;
}

.addEdit-button button span {
  transition: all 0.2s;
  padding-bottom: 3px;
  border-bottom: 1px solid transparent;
}

.addEdit-button::before {
  content: "";
  position: absolute;
  background: url(../img/svg/Add.svg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 18px;
  height: 18px;
  left: 0px;
  top: 7px;
  transition: all 0.3s;
}

.addEdit-button:hover button span {
  border-bottom: 1px solid #0041ff;
}

.switch-field {
  display: flex;
  margin-bottom: 20px;
  overflow: hidden;
}

.switch-field input {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.switch-field input:checked + label {
  background-color: #ffde03;
}

.switch-field label {
  background-color: #fff;
  color: #000;
  font-size: 15px;
  letter-spacing: 0.15px;
  font-weight: 700;
  line-height: 15px;
  text-align: center;
  padding: 7px 13.5px;
  text-transform: uppercase;
  margin-right: -1px;
  border: 1px solid #ffde03;
  transition: all 0.1s ease-in-out;
}

.switch-field label:first-of-type {
  border-radius: 12px 0 0 12px;
}

.switch-field label:last-of-type {
  border-radius: 0 12px 12px 0;
}

.switch-field label:hover {
  cursor: pointer;
}

.custom-radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.custom-radio + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
}

.custom-radio + label::before {
  content: "";
  display: inline-block;
  width: 25px;
  height: 25px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #d7d7d7;
  border-radius: 50%;
  margin-right: 15px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 65% 65%;
  transition: all 0.2s;
  background-color: #fff;
}

.custom-radio:checked + label::before {
  border-color: #000;
  background-color: #fff;
  background-image: url("../img/ellipse-yellow.png");
  transition: all 0.2s;
}

.custom-radio:disabled + label::before {
  background-color: #fff;
}

@media (min-width: 768px) {
  .pos-sticky {
    position: sticky;
    top: 15px;
  }
  .pos-fix {
    position: fixed;
    top: 15px;
  }
  .pos-abs {
    position: absolute;
    bottom: 0;
  }
}

/* Styles -------------------------------------------------------------------- */
.header {
  padding-top: 16px;
  background-color: #fff;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  animation-name: down-opacity;
  animation-duration: 0.75s;
}

.header__menu ul {
  display: flex;
}

.header__menu ul li + li {
  padding-left: 60px;
}

.header__menu ul li a {
  color: #000;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 0.28px;
  transition: all 0.2s;
  position: relative;
}

.header__menu ul li a:after {
  opacity: 0;
  transition: all 0.2s;
  position: absolute;
  content: "";
  width: 37px;
  height: 2px;
  bottom: -26px;
  background-color: #0041ff;
  left: 50%;
  transform: translateX(-50%);
}

.header__menu ul li a:hover {
  color: #0041ff;
}

.header__menu ul li a:hover::after {
  opacity: 1;
}

.header__menu--active .header__menu-btn:before {
  transform: rotate(45deg);
  top: 50%;
}

.header__menu--active .header__menu-btn:after {
  transform: rotate(-45deg);
  top: 50%;
}

.header__menu--active .header__menu-list {
  opacity: 1;
  visibility: visible;
  transition: all 0.55s;
}

.header__menu-btn {
  width: 48px;
  height: 14px;
  background-color: transparent;
  outline: none;
  position: relative;
  transition: all 0.2s;
}

.header__menu-btn:before, .header__menu-btn:after {
  content: '';
  position: absolute;
  left: 0;
  width: 48px;
  height: 1px;
  background-color: #000;
  transition: 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.header__menu-btn:before {
  top: 0;
}

.header__menu-btn:after {
  top: 100%;
}

.header__line {
  padding-top: 18px;
  animation-name: rigth;
  animation-duration: 0.9s;
}

.header__line img {
  height: 1px;
  margin: 0 auto;
  width: 100%;
}

.header__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 177px;
  max-height: 145px;
  margin-top: 10px;
  margin-right: 15px;
  width: 100%;
}

.header__logo a {
  width: 100%;
  display: inline-block;
}

.header__logo a img {
  max-height: 145px;
  width: 100%;
}

.footer {
  font-size: 13px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0.26px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.footer__info {
  padding: 32px 0 28px;
  background-color: #fff;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__address span {
  color: #b2b2b2;
}

.footer__links a span {
  color: #3662ff;
  border-bottom: 1px solid transparent;
  transition: all 0.2s;
  padding-bottom: 2px;
}

.footer__links a span:hover {
  border-bottom: 1px solid #0041ff;
}

.footer__links a + a {
  margin-left: 63px;
}

.footer__copyright {
  text-align: center;
  background-color: #202020;
  padding: 18px 0 17px;
  color: rgba(255, 255, 255, 0.3);
}

.step-one {
  padding-top: 30px;
  padding-bottom: 280px;
}

.step-one__form {
  padding-top: 40px;
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
}

.step-one__form .input-block {
  max-width: 100%;
}

.step-one__inputs {
  display: flex;
  justify-content: space-between;
}

.step-one__inputs .input-block + .input-block {
  margin-left: 14px;
}

.step-one__price-wrapper {
  margin-right: 15px;
}

.step-one__price-wrapper p {
  text-align: left;
  letter-spacing: 0.18px;
  margin-left: 0;
  max-width: 490px;
  padding-top: 0;
}

.step-one__price-wrapper p span {
  font-weight: 700;
  color: #414141;
}

.step-one__form-bottom {
  padding-top: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.step-one .button {
  max-width: 244px;
}

.step-one .checkbox-block {
  max-width: 835px;
}

.step-two {
  padding-top: 30px;
  padding-bottom: 180px;
}

.step-two__form {
  margin: 0 auto;
  max-width: 960px;
  padding-top: 17px;
}

.step-two__inputs {
  position: relative;
  padding-top: 33px;
}

.step-two__inputs span {
  text-align: center;
  max-width: 200px;
  width: 100%;
  margin: 0 auto 45px;
  display: block;
